<template>
  <div class="home">
    <p align="center">
      <img src="../assets/logo.jpg" >
    </p>
    <h2>已上链佛经 - <a class="BlogSubTitle" :href="link" target="_blank" >区块地址</a></h2>
    <div class="fojing">
      <ul>
        <li v-for="(item,index) in indexlist" :key="index">
          <a @click="golink(item.txid)">{{ item.title }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import store from '@/store'
import validator from 'validator'

export default {
  name: 'home',
  store,
  data(){
    return{
      title:store.state.title,
      link:'https://explore.veforge.com/accounts/' + store.state.sender,
      sender:store.state.sender,
      data1:store.state.data1,
    }
  },
  computed:{
    indexlist(){
      return this.$store.state.indexlist;
    }
  },
  methods:{
    getlist(){
      //读取分类

      const filter=connex.thor.filter('transfer')

      filter.criteria([{
        sender: this.sender,
        recipient:this.data1
      }])

      filter.apply(0,256).then(logs=>{

        var arr_txid = []
        store.commit('upindexlist' , 0)

        logs.forEach(function(value,i){

          if( arr_txid.indexOf(value.meta.txID) === -1 ){

            arr_txid.push(value.meta.txID)
            //读取数据
            const transaction=connex.thor.transaction(value.meta.txID)
            transaction.get().then(function(val){

              if( val.clauses ){
                var data = val.clauses[0].data.slice(2)
                var str = new Buffer( data, 'hex').toString('utf8')
                if(validator.isJSON(str)){
                  //json格式 -> 进行判断
                  str = JSON.parse(str)
                  if(str.hasOwnProperty("title") && str.hasOwnProperty("desc") && str.hasOwnProperty("info") && str.info === '佛经在线'){
                      //符合标准，本站提交
                      str.txid = value.meta.txID;
                      store.commit('upindexlist' , str)
                  }
                }
              }
            })

          }
        })

      })
    },
    golink(txid){
        this.$router.push('/info/' + txid)
    }
  },
  mounted() {
    this.getlist()
  },

};
</script>

