import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    title:'佛经在线',
    sender:'0xF62871c5833943c8490317763bF60F3Ec1290C6A', //佛经数据发送
    data1:'0x7777777777777777777777777777777777777777', //佛经数据接收
    data2:'0x4444444444444444444444444444444444444444', //公共数据接收
    signer:'', //授权地址
    indexlist:[], //首页文章列表数据
    mydatalist:[],//我的数据文章列表
  },
  mutations: {
    getsigner(){
      if( !this.state.signer ){
        var signingService = connex.vendor.sign('cert')
        var content = '授权获取您的钱包地址,来源佛经在线;'
        signingService.request({
          purpose: 'identification',
          payload: {
            type: 'text',
            content: content
          }
        }).then(result => {
          this.state.signer = result.annex.signer
        })
      }
    },
    upindexlist(state,val){
      if(!val){
        this.state.indexlist = []
        return
      }
      this.state.indexlist.push(val)
    },
    upmydatalist(state,val){
      if(!val){
        this.state.mydatalist = []
        return
      }
      this.state.mydatalist.push(val)
    },
  },
  actions: {

  },
});
